.root {
  padding: 0.5em;
}

.root textarea {
  height: 5em;
  width: 23em;
  font-family: inherit;
  font-size: inherit;
  margin-right: 3px;
}

.root label {
  white-space: nowrap;
}

.topBar {
  display: grid;
  grid-template-columns: auto auto auto 1fr;
  align-items: center;
  margin-bottom: 0.5em;

  > :last-child {
    justify-self: flex-end;
  }
}

.controlBar {
  margin-bottom: 0.5em;
  display: flex;
  align-items: center;

  button {
    margin-bottom: 3px;
  }
}

.nowTimeDiff {
  margin-left: 0.5em;
}

.root button {
  margin-right: 3px;
}

.version {
  margin-bottom: 0.5em;
  font-size: 0.8em;
  color: #aaa;
}

.selectedRangeText {
  margin-left: 0.5em;
}
