.root {
  margin-top: 0.5em;
}

.jsonEditor {
  height: 20em !important;
  width: 55em !important;
  max-width: calc(100% - 1em);
  font-family: monospace !important;
}

.patternInput {
  font-family: monospace;
  font-size: 1.1em;
}

.inputs {
  display: grid;
  grid-template-columns: 10em 30em 30em;
}

.buttons {
  margin-bottom: 0.5em;
}

.jsonError {
  color: red;
}

.showTags {
  margin-right: 0.5em;

  > select {
    margin-left: 0.2em;
  }
}

.bottomCheck {
  margin-bottom: 0.5em;
}
