.root {
  display: block;
  max-width: 100%;
  overflow: auto;
  pointer-events: bounding-box;
}

.firstOfMonth {
  stroke: #777;
  stroke-width: 3;
  stroke-dasharray: 3, 3;
  fill: transparent;
}

.swatches {
  margin-bottom: 0.9em;
  margin-top: 1.1em;

  > span {
    margin-right: 0.5em;
    padding: 0.15em 0.5em;
  }
}

.sel1 {
  stroke: #777;
  stroke-width: 1;
  stroke-dasharray: 2, 1;
  fill: transparent;
}

.selRange {
  fill: #bbb;
}
