@import "./Tag.scss";

.root {
  display: grid;
  grid-template-columns: 5em 4em 1fr;
  align-items: baseline;
  transition: background-color 500ms;
  padding: 0.1em 0;
}

.editRoot {
  display: flex;
  align-items: center;
  margin: 0.5em 0;
}

.editError {
  border: 1px solid red !important;
  outline: 0;
}

.pendingDelete {
  background-color: mistyrose;
  transition: none;
}

.actions {
  margin-right: 0.8em;
}

.time {
  justify-self: end;
  padding-right: 1em;
  cursor: pointer;
  user-select: none;
}

.itemRelativeTime {
  user-select: none;
  cursor: pointer;
  padding-right: 0.6em;
  color: #999;
}

.linger {
  background-color: #ccffcc;
  transition: all 500ms;
}

.textAndTags {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.swatch {
  display: inline-block;
  white-space: pre;
  margin: 0.15em 0.2em 0.15em 0;
}
