@import "./Tag.scss";

.root {
  margin-top: -0.2em;
  position: relative;
}

.topBorder {
  border-top: 1px solid #ccc;
  position: relative;
  top: -1px;
  margin: 0 -0.5em 0 -0.5em;
}

.dayHeader {
  font-family: var(--dayHeaderFont);
  font-weight: var(--dayHeaderFontWeight);
  font-size: 1.4em;
  background-color: white;
  box-shadow: 0 0.1em 0.2em -0.1em #ccc;
  padding: 0.3em 0 0.3em 0.9em;
  margin: 0 -0.5em 0 -0.5em;
  position: sticky;
  top: 0;
  display: grid;
  grid-template-columns: 1fr auto auto;
  grid-auto-flow: column;
}

.displayDate {
  cursor: pointer;
}

.toggleFullDayButton {
  justify-self: flex-start;
  font-size: 0.7em;
  margin-left: 0.8em;
  align-self: flex-start;
}

.pageButtons {
  align-self: flex-start;
  font-size: 0.7em;
  white-space: nowrap;
  display: grid;
  grid-auto-flow: column;
}

.pageButtons > button,
.toggleFullDayButton {
  margin-right: 0.5em !important;
  height: 2em;
  padding: 0.3em 1em;

  &:disabled {
    path,
    polygon {
      fill: #bbb;
    }
  }
}

.dayHeader + * {
  margin-top: 1em;
}

.root > :last-child {
  margin-bottom: 1.5em;
}

.tagTotals {
  margin-bottom: 0.3em;

  .tag {
    margin-bottom: 0.5em;
  }
}

$gap-color: #ccc;

.gap1 {
  display: inline-block;
  width: 1em;
  margin: 0.1em 0 0.1em 2em;
  border-top: 1px solid $gap-color;
  border-bottom: 1px solid $gap-color;
  border-right: 1px dotted $gap-color;
}

.gap2 {
  display: inline-block;
  width: 1em;
  border-top: 1px solid $gap-color;
  border-bottom: 1px solid $gap-color;
}
